import Vue from 'vue'
import Router from 'vue-router'
import Layout from '@/views/layout/Layout.vue';

Vue.use(Router);

/**
* hidden: true                   if `hidden:true` will not show in the sidebar(default is false)
* redirect: noredirect           if `redirect:noredirect` will no redirct in the breadcrumb
* name:'router-name'             the name is used by <keep-alive> (must set!!!)
* meta : {
   role: ['admin','editor']     will control the page role (you can set multiple roles)
   title: 'title'               the name show in submenu and breadcrumb (recommend set)
   icon: 'svg-name'             the icon show in the sidebar,
   noCache: true                if fasle ,the page will no be cached(default is false)
 }
**/


export const studentRouterMap = [
    {
        path: '/login',
        component: () => import('@/views/login/index.vue'),
        meta: { hidden: true }
    },
    {
        path: '/',
        component: Layout,
        redirect: '/dashboard',
        meta: { hidden: true },
        children: [{
            path: 'dashboard',
            component: () => import('@/views/dashboard/index.vue'),
        }],
    },
    {
        path: '/celetedoldur',
        component: Layout,
        children: [
            {
                path: 'index',
                name: 'CeteleDoldur',
                component: () => import('@/views/cetele/ceteledoldur/index.vue'),
                meta: { title: 'Çetele Doldur', icon: 'form' }
            }
        ]
    }
];

export const teacherRouterMap = [
    {
        path: '/talebeislem',
        component: Layout,
        children: [
            {
                path: 'index',
                name: 'TalebeIslem',
                component: () => import('@/views/talebeislem/index.vue'),
                meta: {
                    title: 'Talebe İşlemleri',
                    icon: 'table',
                    roles: ['TEACHER']
                }
            }
        ]
    }
];



const router = new Router({
    mode: 'history',
    routes: studentRouterMap
});


export default router;